import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import ShowIcon from './ShowIcon';


const Header = styled.div`
  height:92px;
  position:fixed;
  width:100%;
  z-index:3;
`;

const HeaderResponsive = styled.div`
    position:fixed;
    width:100%;
    z-index:3;
    text-align: center;
`;

const Navi = styled.div`
  display:flex;
  height: 100%;
  align-items:center;
`;
const NaviResponsive = styled.div`
  display:flex;
  height: 100%;
  align-items:center;
  padding:.5rem;
  @media only screen and (max-width: 550px) {
    flex-direction: column;
  }
`;
const Portfolio = styled.div`
  width: 50%;
  cursor: pointer;
  color: white;
  position: relative;
  left: 121px;
  font-size: 25px;
  font-family: serif;
`;
const DIV1 = styled.div`
    width: 88%;
    cursor: pointer;
    color: white;
    position: relative;
    font-size: 25px;
    font-family: serif;
    display: flex;
    justify-content: space-between;
    padding-top: .7rem;
    position: relative;
    left: 0px;
`;
const PortfolioResponsive = styled.div`
    width: 88%;
    cursor: pointer;
    color: white;
    position: relative;
    font-size: 25px;
    font-family: serif;
    display: flex;
    position: relative;
    left: 0px;
`;
const Menu = styled.div`
  width: 50%;
  display:flex;
  justify-content: space-evenly;
`;
const MenuResponsive = styled.div`
  width: 50%;
  display:flex;
  justify-content: space-evenly;
  @media only screen and (max-width: 550px) {
    flex-direction: column;
    width:100%;
  }
`;

const Span = styled.span`
  color: white;
  cursor:pointer;
  z-index:3;
  &:hover {
    color:orange;
  }
`;
const SpanResponsive = styled.span`
  color: white;
  cursor:pointer;
  z-index:3;
  &:hover {
    color:orange;
  }
  @media only screen and (max-width: 550px) {
    flex-direction: column;
    width:100%;
    padding:1rem .8rem;
  }
`;

const HeaderComponent = ({setScrollY}) => {

    const[width,setWidth] = useState(window.innerWidth);
    const[toggle,setToggle] = useState(false);

    const onChangeToggle = () => {
      setToggle(!toggle)
      if(!toggle > 0) {
        document.getElementById("header").style.background = '#1b1b1bbd';
      } else {
        document.getElementById("header").style.background = 'none';
      }
    }

    function logit() {
        setWidth(window.innerWidth);
      }    
  
    useEffect(() => {
        function watchScroll() {
            window.addEventListener("resize", logit);
          }
        watchScroll();
    }, [width])

    const goNavi = (title) => {
        switch(title) {
          case "main":
            window.scrollTo({top:0, left:0, behavior:'smooth'});
            setScrollY(0)
            break;
          case "about":
            window.scrollTo({top:510, left:0, behavior:'smooth'});
            setScrollY(510)
            break;
          case "skills":
            window.scrollTo({top:810, left:0, behavior:'smooth'});
            setScrollY(810)
            break;
          case "projects":
            window.scrollTo({top:1110, left:0, behavior:'smooth'});
            setScrollY(1110)
            break;
          case "careers":
            window.scrollTo({top:1410, left:0, behavior:'smooth'});
            setScrollY(1410)
            break;
          default:
            window.scrollTo({top:0, left:0, behavior:'smooth'});
         }
    }

    
    if(width >= 550) {
        return(
            <Header id="header">
                <Navi>
                    <Portfolio onClick={() => goNavi("main")}>Huni's Portfolio</Portfolio>
                    <Menu>
                    <Span onClick={() => goNavi("about")}>about me</Span>
                    <Span onClick={() => goNavi("skills")}>skills</Span>
                    <Span onClick={() => goNavi("projects")}>Project</Span>
                    <Span onClick={() => goNavi("careers")}>careers</Span>
                    </Menu>
                </Navi>
            </Header>
        );
    } else {
        return(
            <HeaderResponsive id="header">
                <NaviResponsive>
                  <DIV1>
                    <PortfolioResponsive onClick={() => goNavi("main")}>Huni's Portfolio
                    </PortfolioResponsive>
                    <div onClick={() => onChangeToggle()} style={{zIndex:10}}>
                      <ShowIcon size="24" index="0" />
                    </div>
                  </DIV1>
                  {toggle &&
                    <MenuResponsive>
                        <SpanResponsive onClick={() => goNavi("about")}>about me</SpanResponsive>
                        <SpanResponsive onClick={() => goNavi("skills")}>skills</SpanResponsive>
                        <SpanResponsive onClick={() => goNavi("projects")}>Project</SpanResponsive>
                        <SpanResponsive onClick={() => goNavi("careers")}>careers</SpanResponsive>
                    </MenuResponsive>
                  }
                </NaviResponsive>
            </HeaderResponsive>
        );
    }
}

export default HeaderComponent;